 function send_form(formid, path) {
    var form_id = '#' + formid;
    var msg = $(form_id).serialize();
    $.ajax({
        type: 'POST',
        url: path,
        data: msg,
        success: function(data) {
            if (data == "send_error") {
                alert('Возникла ошибка при отправке. Попробуйте пожалуйста еще раз.');
            }
            else if (data == "send_success") {
                $(form_id)[0].reset();

                if (formid == 'main_form') {
                    $('.form_wrapper-main').css('display', 'none');
                    $('.form_wrapper-thanks').css('display', 'flex').animate({opacity: 1}, 300);
                }
            }
        },
        error: function(xhr, str){
            alert('Error: ' + xhr.responseCode + ' Please, try again later.');
        }
    });
}


var d = new Date();
var month=new Array("января","февраля","марта","апреля","мая","июня",
"июля","августа","сентября","октября","ноября","декабря");
if(d.getHours() < 20) {
    document.getElementById('date_folder').innerHTML = (d.getDate()+ " " + month[d.getMonth()] + " " + "в 20:00 по МСК");
} else {
    document.getElementById('date_folder').innerHTML = (d.getDate()+ 1 + " " + month[d.getMonth()] + " " + "в 20:00 по МСК");
}
